import React from 'react';

class Signin extends React.Component {

	componentWillMount(){

    if(
      navigator.userAgent.match(/Android/i) ||
      navigator.userAgent.match(/webOS/i) ||
      navigator.userAgent.match(/iPhone/i) ||
      navigator.userAgent.match(/iPod/i) ||
      navigator.userAgent.match(/BlackBerry/i) ||
      navigator.userAgent.match(/Opera Mini/i) ||
      navigator.userAgent.match(/IEMobile/i) ||
      navigator.userAgent.match(/Windows Phone/i)
      ){
        this.setState({types:'mobile'});}
      else{

        this.setState({types:'desk'});
      }


    this.setState({inWidths:window.innerWidth});
    this.setState({inHeights:window.innerHeight});
    if(window.innerHeight>window.innerWidth){
      this.setState({checks:0});
      }else{
        this.setState({checks:1});
    }

}

	componentDidMount(){
    window.eccolo.addEventListener("keydown", this.funzioneKBEventEccolo)



		if(true){
			var url ="https://dolfo.altervista.org/verso/carica.php"
			var xmlhttp = new XMLHttpRequest();
			xmlhttp.open("GET",url,true);
    	xmlhttp.onload = this.iAmOnlineForReal;
    	xmlhttp.onerror = this.iAmOfflineForReal;
			xmlhttp.send();
		}


	}

	constructor(){
		super();
		this.state={
			signInEmail_Nome:'',
			signInPassword:'',
			style:'',
			types:'',
			inWidths:null,
			inHeights:null,
			checks:null,
			width80:'width80'
		}
	}

	setOnline = () => { //check internet
		this.setState({amIOnline:true})
	}


	setOffline = () => { //check internet
		this.setState({amIOnline:false})
	}

	iAmOfflineForReal = () =>	{
		this.setState({amIOnlineForReal:false})
		this.setOffline();
		this.onAccessTypeChange();
	}

	iAmOnlineForReal = () =>	{
		this.setState({amIOnlineForReal:true})
		this.setOnline();
	}

	onEmailChange = (event) => {
		this.setState({signInEmail_Nome:event.target.value})
	}

	onPasswordChange = (event) => {
		this.setState({signInPassword:event.target.value})
	}

	onSubmitSignIn = () =>{
		let noError = true;
		window.caric.classList.remove("displayNone");
		window.eccolo.classList.remove("sam")
		window.h1Caric.innerHTML = "Provo a connettermi...";

		setTimeout(function(){if(window.eccolo&&noError&&window.h1Caric.innerHTML!=="Username o Password errata")
		window.h1Caric.innerHTML= "Colgo le rose..."}, 2500);
		setTimeout(function(){if(window.eccolo&&noError&&window.h1Caric.innerHTML!=="Username o Password errata")
		window.h1Caric.innerHTML= "Mangio i broccoli..."}, 5000);
		setTimeout(function(){if(window.eccolo&&noError&&window.h1Caric.innerHTML!=="Username o Password errata")
		window.h1Caric.innerHTML= "NaNaNaNaNaNaNaNaNaNa"}, 7500);
		setTimeout(function(){if(window.eccolo&&noError&&window.h1Caric.innerHTML!=="Username o Password errata")
		window.h1Caric.innerHTML= "Buono il succhetto alla pera"}, 10000);
		setTimeout(function(){if(window.eccolo&&noError&&window.h1Caric.innerHTML!=="Username o Password errata")
		window.h1Caric.innerHTML= "Mangio i broccoli..."}, 12500);
		setTimeout(function(){if(window.eccolo&&noError&&window.h1Caric.innerHTML!=="Username o Password errata")
		window.h1Caric.innerHTML= "Ci sta mettendo tanto.. :/"}, 15000);
		setTimeout(function(){if(window.eccolo&&noError&&window.h1Caric.innerHTML!=="Username o Password errata")
		window.h1Caric.innerHTML= "FRESHHH"}, 17500);
		setTimeout(function(){if(window.eccolo&&noError&&window.h1Caric.innerHTML!=="Username o Password errata")
		window.h1Caric.innerHTML= "Mangio i broccoli..."}, 20000);
		setTimeout(function(){if(window.eccolo&&noError&&window.h1Caric.innerHTML!=="Username o Password errata")
		window.h1Caric.innerHTML= "NaNaNaNaNaNaNaNaNaNa"}, 22500);
		setTimeout(function(){if(window.eccolo&&noError&&window.h1Caric.innerHTML!=="Username o Password errata")
		window.h1Caric.innerHTML= "Buono il succhetto alla pera"}, 25000);
		setTimeout(function(){if(window.eccolo&&noError&&window.h1Caric.innerHTML!=="Username o Password errata")
		window.h1Caric.innerHTML= "Mangio i broccoli..."}, 27500);
		setTimeout(function(){if(window.eccolo&&noError&&window.h1Caric.innerHTML!=="Username o Password errata")
		window.h1Caric.innerHTML= "Ci sta mettendo tanto.. :/"}, 30000);

		let URLHEROKUSignin = "https://agile-forest-45123.herokuapp.com/signin";

		if(this.state.amIOnline){

		}else{
			URLHEROKUSignin = "";
		}

		fetch( URLHEROKUSignin, {
			method:'POST',
			headers:{'Content-Type':'application/json'},
			body: JSON.stringify({email_nome:this.state.signInEmail_Nome.trim(), password:this.state.signInPassword})
		})
		.then(resp=>resp.json())
		.then(user => {
			noError = false;
			window.h1Caric.innerHTML = "Ci sono quasi...";
        if(user.id){
					window.h1Caric.innerHTML = "Connesso!!!";
          this.props.accessoUtente(user);
        }else{

						if(window.eccolo.classList.contains("sam"))
							window.eccolo.classList.remove("sam")
							else
							window.eccolo.classList.add("sam")


					window.h1Caric.innerHTML = "Username o Password errata";

				}
      }).catch(error=>{
				console.log(error);
				this.loginOffline(error);
			})
	}

	checkPermission = (usr, pass) => {
		if(((usr ==="Sam")||(usr==="sam")||(usr==="samuele")||(usr==="Samuele"))&&(pass==="molikode09"))
			return true;

		if(((usr ==="Broccola")||(usr==="broccola"))&&(pass==="lapassword"))
				return true;

			return false;



	}

	loginOffline = (error) =>{
		let allowed = false;
		allowed = this.checkPermission(this.state.signInEmail_Nome, this.state.signInPassword)
		let user = {isOffline: true, nome: this.state.signInEmail_Nome}
		if(allowed)
			this.props.accessoUtente(user);
			else{
				if(window.eccolo.classList.contains("sam"))
					window.eccolo.classList.remove("sam")
				else
					window.eccolo.classList.add("sam")

				window.h1Caric.innerHTML = "Username o Password errata";
			}
	}

	onAccessTypeChange = () =>{
		if(this.state.amIOnlineForReal){
			if(window.divFiglioInput.innerHTML==="online"){
			window.divFiglioInput.innerHTML="offline";
			window.divFiglioInput.classList.toggle("bg-white-30")
			window.divFiglioInput.classList.toggle("divFiglioInputOffline");
			window.divFiglioInput.classList.toggle("divFiglioInputOnline");
			this.setOffline();
		}else{
			window.divFiglioInput.innerHTML="online";
			window.divFiglioInput.classList.toggle("bg-white-30");
			window.divFiglioInput.classList.toggle("divFiglioInputOffline");
			window.divFiglioInput.classList.toggle("divFiglioInputOnline");
			this.setOnline();

		}}else{
			if(window.divFiglioInput.innerHTML==="online"){
			window.divFiglioInput.innerHTML="offline";
			window.divFiglioInput.classList.toggle("bg-white-30")
			window.divFiglioInput.classList.toggle("divFiglioInputOffline");
			window.divFiglioInput.classList.toggle("divFiglioInputOnline");
			this.setOffline();
		}}


	}

	funzioneKBEventEccolo = (event) => {

	    if(event.which===13){
	      this.onSubmitSignIn();
	    }


	    if(this.state.type==="mobile"&&event.which===13)
	      window.barracerca.blur();


  	}


	render(){
		if(this.state.types==="mobile"&&this.state.checks===0){

		return(

			<article id="accediArticle" style={{width:'80%'}} className={"br3 ba dark-gray b--black-10 mv4 w-100 w-50-m w-25-l mw6 shadow-5 center"}>
				<main id="eccolo" className={this.state.style+" pa4 black-80"}>
				  <div className="measure">
				    <fieldset id="sign_up" className=" ba b--transparent ph0 mh0">
				      <legend className="f1 fw6 ph0 mh0">Accedi</legend>
				      <div  className="mt3">
				        <label className="db fw6 lh-copy f6" htmlFor="email-address">Email o Username</label>
				        <input onChange={this.onEmailChange} className="pa2 input-reset ba bg-transparent  hover-white w-100" type="text" name="email-address"  id="email-address"/>
				      </div>
				      <div   className="mv3">
				        <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
				        <input onChange={this.onPasswordChange} className="b pa2 input-reset ba bg-transparent  hover-white w-100" type="password" name="password"  id="password"/>
				      </div>
											<div className="mv3">
												<label className="db fw6 lh-copy f6" htmlFor="accesso">Accesso</label>
												<div id="divPadreInput" className="br4 pa2 input-reset ba w-100">
														<div onClick={this.onAccessTypeChange} id="divFiglioInput" className="pointer br4 b pa1 input-reset ba bg-white-30  w-50 divFiglioInputOnline">
														online
														</div>
												</div>
											</div>
				    </fieldset>
				    <div  className="">
				      <input onClick={this.onSubmitSignIn} className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" type="submit" value="Accedi"/>
				      <div id="caric"className="displayNone"><h3 id="h1Caric">Caricamento...</h3></div>
				    </div>

				  </div>
				</main>
			</article>
		);
	}else{

		return(

			<article id="accediArticle" className="br3 ba dark-gray b--black-10 mv4 w-100 w-50-m w-25-l mw6 shadow-5 center">
				<main id="eccolo" className={this.state.style+" pa4 black-80"}>
				  <div className="measure">
				    <fieldset id="sign_up" className=" ba b--transparent ph0 mh0">
				      <legend className="f1 fw6 ph0 mh0">Accedi</legend>
				      <div  className="mt3">
				        <label className="db fw6 lh-copy f6" htmlFor="email-address">Email o Username</label>
				        <input onChange={this.onEmailChange} className="pa2 input-reset ba bg-transparent  hover-white w-100" type="text" name="email-address"  id="email-address"/>
				      </div>
				      <div   className="mv3">
				        <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
				        <input onChange={this.onPasswordChange} className="b pa2 input-reset ba bg-transparent  hover-white w-100" type="password" name="password"  id="password"/>
				      </div>
							<div className="mv3">
								<label className="db fw6 lh-copy f6" htmlFor="accesso">Accesso</label>
								<div id="divPadreInput" className="br4 pa2 input-reset ba w-100">
										<div onClick={this.onAccessTypeChange} id="divFiglioInput" className="pointer br4 b pa1 input-reset ba bg-white-30  w-50 divFiglioInputOnline">
										online
										</div>
								</div>
							</div>

				    </fieldset>
				    <div  className="">
				      <input onClick={this.onSubmitSignIn} className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" type="submit" value="Accedi"/>
				      <div id="caric" className="displayNone"><h3 id="h1Caric">Caricamento...</h3></div>
				    </div>

				  </div>
				</main>
			</article>
		);
	}
	}
}


export default Signin;



