import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import registerServiceWorker from './registerServiceWorker';
import Particles from 'react-particles-js';
const params={
  parParmobile: {"particles": {"number": {"value": 35,"density": {  "enable": false,  "value_area": 800}},"color": {"value": "#ffffff"},"shape": {"type": "circle","stroke": {  "width": 0,  "color": "#000000" }, "polygon": {  "nb_sides": 5},"image": {  "src": "",  "width": 100,  "height": 100}},"opacity": {"value": 0.2498083922587271,"random": true,"anim": {  "enable": false,  "speed": 1,  "opacity_min": 0.01,  "sync": false}},"size": {"value": 34.5,"random": true,"anim": {  "enable": false,"speed": 40,  "size_min": 0.1,  "sync": false}},"line_linked": {"enable": false,"distance": 150,"color": "#ffffff","opacity": 0.4,"width": 1},"move": {"enable": true,"speed": 1.85,"direction": "top-right","random": false,"straight": false,"out_mode": "out","bounce": false,"attract": {  "enable": false,  "rotateX": 600,  "rotateY": 1200}}},"interactivity": {"detect_on": "window","events": {"onhover": {  "enable": false,  "mode": "repulse"},"onclick": {  "enable": true,  "mode": "repulse"},"resize": true},"modes": {"grab": {  "distance": 400,  "line_linked": {    "opacity": 1  }},"bubble": {  "distance": 400,  "size": 40,  "duration": 2,  "opacity": 8,  "speed": 3},"repulse": {  "distance": 50,  "duration": 0.4},"push": {  "particles_nb": 4},"remove": {  "particles_nb": 2}}},"retina_detect": true},
  parPardesk: {"particles": {"number": {"value": 60,"density": {"enable": false,"value_area": 800}},"color": {"value": "#ffffff"},"shape": {"type": "circle","stroke": {"width": 0,"color": "#000000"},"polygon": {  "nb_sides": 5},"image": {"src": "","width": 100,"height": 100}},"opacity": {"value": 0.2498083922587271,"random": true,"anim": {"enable": false,"speed": 1,"opacity_min": 0.01,"sync": false}},"size": {"value": 34.5,"random": true,"anim": {  "enable": false,  "speed": 40,  "size_min":0.1,  "sync": false}},"line_linked": {"enable": false,"distance": 150,"color": "#ffffff","opacity": 0.4,"width": 1},"move": {"enable": true,"speed": 1.85,"direction": "top-right","random": false,"straight": false,"out_mode": "out","bounce": false,"attract": {"enable": false,"rotateX": 600,"rotateY": 1200}}},"interactivity": {"detect_on": "window","events": {"onhover": {  "enable": true,  "mode": "repulse"},"onclick": {  "enable": true,  "mode": "repulse"},"resize": true},"modes": {"grab": {  "distance": 400,  "line_linked": {    "opacity": 1  }},"bubble": {  "distance": 400,  "size": 40,  "duration": 2,  "opacity": 8,  "speed": 3},"repulse": {  "distance": 100,  "duration": 0.4},"push": {  "particles_nb": 4},"remove": {  "particles_nb": 2}}},"retina_detect": true}}
  let paramsParticles = null;
if(
  navigator.userAgent.match(/Android/i) ||
  navigator.userAgent.match(/webOS/i) ||
  navigator.userAgent.match(/iPhone/i) ||
  navigator.userAgent.match(/iPod/i) ||
  navigator.userAgent.match(/BlackBerry/i) ||
  navigator.userAgent.match(/Opera Mini/i) ||
  navigator.userAgent.match(/IEMobile/i) ||
  navigator.userAgent.match(/Windows Phone/i)
  ){
    paramsParticles = params.parParmobile;
  }
  else{
    paramsParticles = params.parPardesk;
  }


ReactDOM.render(<div><Particles className="particles" params={paramsParticles} /> <App /></div>, document.getElementById('root'));
registerServiceWorker();



