  funzioneKBEvent = (event) => {
    if(event.code==="ArrowRight"){
      //this.divImgMenoFrecceClick();
    }
    else if(event.code==="ArrowLeft"){
      //this.divImgMenoFrecceClickLeft();
    }
    if(event.code==="Escape"||event.code==="Backspace"||event.code==="Delete"){
      if(this.state.modalita==="sfondogallery")this.outClick();
      if(this.state.menuState==="ciao")this.mostraMenu();
    }

    if(this.state.type==="mobile"&&this.state.access !== 'negato'&&event.which===13)
      window.barracerca.blur();

    //console.log(event)
  }


  backButtonClick = (event) => {

    if(this.state.modalita==="sfondogallery")
      this.outClick();
      else if(this.state.form==="carica")
        this.nonCarica();
      else
        window.history.back()

  }



  outClick = (event) => {
    this.setState({modalita:""});
    //window.caricaWindow.classList.replace("zIndexlow","zIndexhigh");

    this.setState({overflow:"overflowAuto"});
    if(window.imgCerca){
    window.imgCerca.classList.toggle("z-1")
    window.imgCarica.classList.toggle("z-1")
    }
  }

  updateFinestra = (event) => {
    this.setState({modalita:""});
  }

  leftClick = (event) => {


      let index = this.state.currentid;
       index = Number(index);
      for (var i = this.state.robotsFil.length - 1; i >= 0; i--) {

        if(this.state.robotsFil[i].id===index&&i!==this.state.robotsFil.length-1){


        index = this.state.robotsFil[(i+1)].id;
        break;


        }
      }
      this.setState({currentid:(index)});
  }

  rightClick = (event) => {



      let index = this.state.currentid;
       index = Number(index);
       if(index>=0){}
      for (var i = this.state.robotsFil.length - 1; i >= 0; i--) {

        if(this.state.robotsFil[i].id===index&&i!==0){


        index = this.state.robotsFil[(i-1)].id;
        break;


        }
      }
      this.setState({currentid:(index)});





  }


